* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  background-image: url("fondo.b9cf7e7d.png");
  height: 100vh;
  font-family: American Typewriter, serif;
  overflow: hidden;
}

.welcome {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.welcome-title {
  color: #009048;
  font-size: 80px;
  font-weight: 700;
}

.welcome-title-letter {
  color: #91ccaf;
}

.start-button {
  color: #d8fcfc;
  text-align: center;
  cursor: pointer;
  background-color: #006cfc;
  border: 10px solid #001997;
  border-radius: 10px;
  margin: 50px 0;
  padding: 10px 80px;
  font-family: Odibee Sans;
  font-size: 45px;
  font-weight: 400;
}

.start-button:hover {
  color: #fff;
  background-color: #009048;
  border: 10px solid #004221;
  transition: all .5s;
}

.hands {
  justify-content: center;
  gap: 20px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: -50px;
  transform: translateY(15px);
}

.hand {
  cursor: pointer;
}

@media (width <= 460px) {
  .hand {
    transform: scale(.7);
  }
}

@media (width <= 350px) {
  .hands {
    gap: 5px;
    transform: translateY(50px);
  }
}
